import {
  Box,
  Divider,
  IconButton,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Checkbox,
  Stack,
  Typography,
} from "@mui/material";
import DeleteIcon from "@mui/icons-material/Delete";
import EditIcon from "@mui/icons-material/Edit";

import { Link } from "react-router-dom";
import { format } from "date-fns";

export default function ListView(props) {
  return (
    <Box style={{ marginTop: 10 }}>
      <Typography variant="h5" style={{ marginBottom: 10 }}>
        Task list
      </Typography>

      <List>
        {props.filtered.map((record, index) => {
          return (
            <ListItem
              key={index}
              style={{
                marginBottom: 10,
                padding: 0,
                paddingLeft: 5,
                backgroundColor: "lightGray",
              }}
            >
              <ListItemIcon>
                <Checkbox
                  checked={record.done}
                  onChange={() => {
                    props.toggleDone(record.id);
                  }}
                />
              </ListItemIcon>

              <ListItemText
                primary={record.title}
                secondary={
                  record.date ? <>{format(record.date, "d.M.y")}</> : null
                }
              />

              <Stack direction="row" spacing={-3}>
                <ListItemIcon>
                  <IconButton
                    color="primary"
                    component={Link}
                    to={`edit/${record.id}`}
                  >
                    <EditIcon />
                  </IconButton>
                </ListItemIcon>
                <ListItemIcon>
                  <IconButton
                    color="error"
                    component={Link}
                    to={`delete/${record.id}`}
                  >
                    <DeleteIcon />
                  </IconButton>
                </ListItemIcon>
              </Stack>
              <Divider />
            </ListItem>
          );
        })}
      </List>
    </Box>
  );
}
