import { useState } from "react";
import {
  AppBar,
  IconButton,
  Toolbar,
  CssBaseline,
  Drawer,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  Typography,
} from "@mui/material";
import AddIcon from "@mui/icons-material/Add";
import MenuIcon from "@mui/icons-material/Menu";
import CheckBoxIcon from "@mui/icons-material/CheckBox";
import WatchLaterIcon from "@mui/icons-material/WatchLater";
import TodayIcon from "@mui/icons-material/Today";
import BallotIcon from "@mui/icons-material/Ballot";
import CheckBoxOutlineBlankIcon from "@mui/icons-material/CheckBoxOutlineBlank";
import { Link } from "react-router-dom";

function Navigation(props) {
  const [naytaValikko, setNaytaValikko] = useState(false);

  return (
    <CssBaseline>
      <AppBar position="static">
        <Toolbar>
          <IconButton
            edge="start"
            color="inherit"
            onClick={() => {
              setNaytaValikko(true);
            }}
          >
            <MenuIcon />
          </IconButton>

          <Typography variant="h6" sx={{ flexGrow: 1 }}>
            {props.filter}
          </Typography>

          <IconButton component={Link} to="add" type="submit" color="inherit">
            <AddIcon />
          </IconButton>

          <Drawer
            open={naytaValikko}
            onClose={() => {
              setNaytaValikko(false);
            }}
          >
            <List
              style={{ width: 220, marginTop: 50 }}
              onClick={() => {
                setNaytaValikko(false);
              }}
            >
              <ListItem
                button
                onClick={() => {
                  props.setFilter("All");
                }}
              >
                <ListItemIcon>
                  <BallotIcon />
                </ListItemIcon>
                <ListItemText primary="All" />
              </ListItem>

              <ListItem
                button
                onClick={() => {
                  props.setFilter("Today");
                }}
              >
                <ListItemIcon>
                  <TodayIcon />
                </ListItemIcon>
                <ListItemText primary="Today" />
              </ListItem>

              <ListItem
                button
                onClick={() => {
                  props.setFilter("Later");
                }}
              >
                <ListItemIcon>
                  <WatchLaterIcon />
                </ListItemIcon>
                <ListItemText primary="Later" />
              </ListItem>

              <ListItem
                button
                onClick={() => {
                  props.setFilter("Anytime");
                }}
              >
                <ListItemIcon>
                  <CheckBoxOutlineBlankIcon />
                </ListItemIcon>
                <ListItemText primary="Anytime" />
              </ListItem>

              <ListItem
                button
                onClick={() => {
                  props.setFilter("Completed");
                }}
              >
                <ListItemIcon>
                  <CheckBoxIcon />
                </ListItemIcon>
                <ListItemText primary="Completed" />
              </ListItem>
            </List>
          </Drawer>
        </Toolbar>
      </AppBar>
    </CssBaseline>
  );
}

export default Navigation;
