/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from "@emotion/react";
import { useRef, useState } from "react";
import { Button, Stack, TextField, Box, Typography } from "@mui/material";
import { Link, useNavigate } from "react-router-dom";
import { v4 as uuid } from "uuid";
import DatePicker from "@mui/lab/DatePicker";
import AdapterDateFns from "@mui/lab/AdapterDateFns";
import LocalizationProvider from "@mui/lab/LocalizationProvider";
import { fi } from "date-fns/locale";

export default function AddView(props) {
  const navigate = useNavigate();

  const title = useRef();
  const [date, setDate] = useState(null);

  const [errorMsg, setErrorMsg] = useState({});

  const handleForm = (event) => {
    event.preventDefault();

    let errors = validateInput(title, date);

    if (Object.entries(errors).length > 0) {
      setErrorMsg({ ...errors });
    } else {
      setErrorMsg({});
      addRecord();
      navigate("/");
    }

    function addRecord() {
      let newRecord = {
        id: uuid(),
        title: title.current.value,
        date: date ? date.getTime() : null,
        done: false,
      };

      let sorted = [...props.records, newRecord].sort((a, b) => {
        return a.date - b.date;
      });

      props.setRecords(sorted);
    }
  };

  return (
    <Box style={{ marginTop: 10 }}>
      <Typography variant="h5" style={{ marginBottom: 10 }}>
        Adding a task
      </Typography>

      <form onSubmit={handleForm}>
        <Stack spacing={2}>
          <TextField
            style={{ color: "#ffd700" }}
            label="Title"
            inputRef={title}
            fullWidth={true}
            error={Boolean(errorMsg.title)}
            helperText={errorMsg.title}
          />

          <LocalizationProvider dateAdapter={AdapterDateFns} locale={fi}>
            <DatePicker
              renderInput={(params) => (
                <TextField
                  {...params}
                  error={Boolean(errorMsg.date)}
                  helperText={errorMsg.date}
                />
              )}
              value={date}
              fullWidth
              mask={null}
              label="Date"
              onChange={setDate}
            />
          </LocalizationProvider>

          <Stack direction="row" spacing={2} css={{ marginTop: 10 }}>
            <Button type="submit" variant="contained" fullWidth>
              Add
            </Button>

            <Button component={Link} to="/" variant="outlined" fullWidth>
              Cancel
            </Button>
          </Stack>
        </Stack>
      </form>
    </Box>
  );
}

function validateInput(title, date) {
  let errors = {};

  if (!title.current.value) {
    errors = { ...errors, title: "Title is missing" };
  }

  if (date && isNaN(date)) {
    errors = { ...errors, date: "Date error" };
  }

  return errors;
}
