import { useEffect, useState } from "react";
import { Container } from "@mui/material";
import { Route, Routes } from "react-router-dom";
import { isAfter } from "date-fns";

import AddView from "./components/AddView";
import AppTitle from "./components/AppTitle";
import DeleteView from "./components/DeleteView";
import EditView from "./components/EditView";
import ListView from "./components/ListView";
import Navigation from "./components/Navigation";

export default function App() {
  const [records, setRecords] = useState([]);
  const [filter, setFilter] = useState("Today");

  const filtered = records.filter((record) => {
    if (filter === "All") {
      return true;
    } else if (filter === "Today" && !record.done && record.date) {
      return !isAfter(record.date, new Date());
    } else if (filter === "Completed" && record.done === true) {
      return true;
    } else if (filter === "Later" && !record.done && record.date) {
      return isAfter(record.date, new Date());
    }
    if (filter === "Anytime" && !record.done && !record.date) {
      return true;
    } else {
      return false;
    }
  });

  const toggleDone = (id) => {
    let currentRecord = records.find((record) => record.id === id);
    currentRecord.done = !currentRecord.done;
    setRecords([...records]);
  };

  const openRecords = () => {
    if (localStorage.getItem("records")) {
      setRecords(JSON.parse(localStorage.getItem("records")));
    } else {
      setRecords([]);
    }
  };

  const saveRecords = () => {
    localStorage.setItem("records", JSON.stringify(records));
  };

  useEffect(() => {
    openRecords();
  }, []);

  useEffect(() => {
    saveRecords();
  }, [records]);

  return (
    <Container fixed={true} maxWidth="xs">
      <AppTitle />

      <Navigation filter={filter} setFilter={setFilter} />

      <Routes>
        <Route
          path="/"
          element={
            <ListView
              filtered={filtered}
              toggleDone={toggleDone}
              // setRecords={setRecords}
            />
          }
        />

        <Route
          path="add"
          element={<AddView records={records} setRecords={setRecords} />}
        />

        <Route
          path="/edit/:id"
          element={<EditView records={records} setRecords={setRecords} />}
        />

        <Route
          path="/delete/:id"
          element={<DeleteView records={records} setRecords={setRecords} />}
        />
      </Routes>
    </Container>
  );
}
