/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from "@emotion/react";
import { Button, Stack, Box, Typography } from "@mui/material";
import { Link, useNavigate, useParams } from "react-router-dom";
import { format } from "date-fns";

export default function DeleteView(props) {
  const navigate = useNavigate();
  const { id } = useParams();

  const currentRecord = props.records.find((record) => record.id === id);

  const handleForm = (event) => {
    event.preventDefault();

    let records = props.records.filter((record) => {
      return record.id !== id;
    });

    props.setRecords([...records]);

    navigate("/");
  };

  return (
    <Box style={{ marginTop: 10 }}>
      <Typography variant="h5" style={{ marginBottom: 10 }}>
        Deleting a task
      </Typography>

      <Typography style={{ marginBottom: 10 }}>
        Do you want to delet task "{currentRecord.title}"
        {currentRecord.date ? ", " + format(currentRecord.date, "d.M.y") : null}
        ?
      </Typography>

      <form onSubmit={handleForm}>
        <Stack spacing={2}>
          <Stack direction="row" spacing={2} css={{ marginTop: 10 }}>
            <Button type="submit" variant="contained" fullWidth>
              Delete
            </Button>

            <Button component={Link} to="/" variant="outlined" fullWidth>
              Cancel
            </Button>
          </Stack>
        </Stack>
      </form>
    </Box>
  );
}
